/* General form container styling */
.formContainer {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;
    padding: 20px;
    background-color: #f9f9f9;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    max-width: 800px;
    margin: 0 auto;
}

/* Styling for the close button */
button[aria-label="Close form"] {
    background-color: transparent;
    border: none;
    cursor: pointer;
    font-size: 1.5rem;
    color: #ff6b6b;
    transition: color 0.3s;
}

button[aria-label="Close form"]:hover {
    color: #ff3b3b;
}

/* Room number and nights input styling */
.roomNumber,
.price,
.personalInfo,
.comments {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 20px;
}

/* Styling for individual input fields */
.roomNumberInput,
.numbernights,
.checks,
.priceInputs,
.personalInput,
.commentsStyles {
    width: 100%;
    padding: 10px;
    border: 1px solid #ccc;
    border-radius: 4px;
    font-size: 1rem;
    transition: border-color 0.3s;
}

.roomNumberInput:focus,
.numbernights:focus,
.checks:focus,
.priceInputs:focus,
.personalInput:focus,
.commentsStyles:focus {
    border-color: #007bff;
    outline: none;
}

/* Styling for labels */
label {
    font-weight: bold;
    margin-bottom: 5px;
    display: block;
}

/* Error message styling */
.errorContainer {
    color: #ff6b6b;
    font-size: 0.875rem;
    margin-top: 5px;
}

/* Submit button styling */
.submitButton {
    grid-column: span 2;
    display: flex;
    justify-content: center;
    margin-top: 20px;
}

input[type="submit"].closeButton {
    background-color: #007bff;
    color: #fff;
    padding: 10px 20px;
    border: none;
    border-radius: 4px;
    cursor: pointer;
    font-size: 1rem;
    transition: background-color 0.3s;
}

input[type="submit"].closeButton:hover {
    background-color: #0056b3;
}

/* Responsive adjustments */
@media (max-width: 600px) {
    .formContainer {
        grid-template-columns: 1fr;
    }

    .roomNumber,
    .price,
    .personalInfo,
    .comments {
        grid-template-columns: 1fr;
    }

    .submitButton {
        grid-column: span 1;
    }
}