.form-container {
    position: absolute;
    left: 50%;
    top: 40%;
    transform: translate(-50%, -50%);
    z-index: 4;
    /* background-color: rgb(236, 27, 27); */

    /* Set the width to 70% of the viewport width */
    height: 70vh;
    /* Set the height to 70% of the viewport height */
}

/* Default width for large screens */
@media (min-width: 1024px) {
    .form-container {
        width: 100%;
    }
}


@media (max-width: 1023px) {
    .form-container {
        width: 80%;
    }
}


@media (max-width: 600px) {
    .form-container {
        width: 95%;
        left: 2.5%;
        top: 10%;
    }
}