.formContainer {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    /* grid-template-columns: repeat(3, 1fr); */
    gap: 10px;
    background-color: #2196F3;
    padding: 20px;
    position: absolute;
    left: 55%;
    top: 45%;
    transform: translate(-50%, -50%);
    z-index: 4;
    background-color: rgba(0, 0, 0, 0.3);
    width: 60%;

}

.formContainer>div {
    background-color: rgba(255, 255, 255, 0.8);
    text-align: center;
    padding: 10px;
    font-size: 12px;
    width: 100%;
}

.roomNumber {
    display: grid;
    grid-template-columns: repeat(2, minmax(50px, 1fr));
    /* grid-template-rows: repeat(2, minmax(50px, 1fr)); */
    gap: 10px;
    width: 100%;
    height: auto;
}

.roomNumberInput {
    width: 80%;
    min-width: 10px;

}

.price {
    display: grid;
    grid-template-columns: repeat(2, minmax(50px, 1fr));
    /* grid-template-rows: repeat(2, minmax(50px, 1fr)); */
    gap: 10px;
    width: 100%;
    height: auto;

    /* height: fit-content; */
}

.priceInputs {
    width: 80%;
    min-width: 10px;
}

.checks {
    min-width: 10px;
    width: 80%;
}

.personalInfo {
    display: grid;
    grid-template-columns: repeat(2, minmax(50px, 1fr));
    gap: 5px;

}

.personalInput {
    min-width: 10px;
    width: 100%;

}

.dates {
    display: grid;
    grid-template-columns: repeat(2, minmax(150px, 1fr));
    gap: 10px;
    width: 100%;
}

.comments {
    display: grid;
    grid-template-columns: repeat(1, 1fr);
    gap: 10px;
    grid-column: 1 / 3;
    align-items: center;
    /* Centers content vertically */
    width: 100%;
    /* justify-content: start; */
    /* Aligns content to the start (left) */
}

.commentsStyles {
    /* align-items: start; */
    /* justify-content: start; */
    width: 100%;

}

.numbernights {
    width: 80%;
}



.closeButton {
    grid-area: close;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
    color: white;
    font-size: 16px;
    background-color: #ff0000;
    border: none;
    height: 40px;
    width: 100%;
}

.closeButton:hover {
    background-color: #cc0000;

}



@media (max-width: 600px) {
    .formContainer {
        grid-template-columns: repeat(2, 1fr);
    }

    .personalInfo {
        grid-template-columns: repeat(1, 1fr);
    }
}